export default {

  components: {
    VueApexCharts: () => import('vue-apexcharts')
  },

  props: {
    dataset: {
      type: Array,
      required: true
    },
    title: {
      type: Object,
      required: true
    }
  },

  data () {
    return {
      chartOptions: {
        chart: {
          id: 'boxplot-1',
          type: 'boxPlot',
          height: 350
        },
        yaxis: {
          decimalsInFloat: 2
        },
        tooltip: {
          enebled: false,
          x: {
            show: true
          },
          y: {
            formatter: (value) => {
              return value.toFixed(2)
            }
          }
        },
        plotOptions: {
          boxPlot: {
            colors: {
              upper: '#5C4742',
              lower: '#A5978B'
            }
          }

        }
      }
    }
  },
  created () {
    this.chartOptions.title = this.title
  },

  methods: {
    getChartOptions () {
      return this.chartOptions
    }
  }
}
